import React, { useRef } from 'react';
import SectionHeader from './SectionHeader';
import { Documents, UploadedFile } from '../types';
import { Translations } from '../types/translations';

interface Props {
  documents: Documents;
  onChange: (documents: Documents) => void;
  t: Translations;
}

const DocumentsSection: React.FC<Props> = ({ documents, onChange, t }) => {
  const f = t.fields;
  const billOfLadingRef = useRef<HTMLInputElement>(null);
  const commercialInvoiceRef = useRef<HTMLInputElement>(null);
  const exportInvoiceRef = useRef<HTMLInputElement>(null);
  const otherDocumentRef = useRef<HTMLInputElement>(null);

  const formatFileSize = (bytes: number): string => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
  };

  const handleFileChange = (
    fileType: keyof Documents,
    e: React.ChangeEvent<HTMLInputElement>
  ) => {
    const file = e.target.files?.[0] || null;
    if (file) {
      onChange({
        ...documents,
        [fileType]: {
          file,
          fileName: file.name,
          fileSize: file.size,
        },
      });
    }
  };

  const handleRemoveFile = (fileType: keyof Documents) => {
    onChange({
      ...documents,
      [fileType]: null,
    });
    // Reset the input
    const refs: Record<keyof Documents, React.RefObject<HTMLInputElement | null>> = {
      billOfLading: billOfLadingRef,
      commercialInvoice: commercialInvoiceRef,
      exportInvoice: exportInvoiceRef,
      otherDocument: otherDocumentRef,
    };
    if (refs[fileType].current) {
      refs[fileType].current!.value = '';
    }
  };

  const FileUploadField = ({
    label,
    fileType,
    file,
    inputRef,
  }: {
    label: string;
    fileType: keyof Documents;
    file: UploadedFile | null;
    inputRef: React.RefObject<HTMLInputElement | null>;
  }) => (
    <div className="file-upload-field">
      <label className="field-label">{label}</label>
      <div className="file-upload-container">
        <input
          ref={inputRef}
          type="file"
          accept=".pdf,.jpg,.jpeg,.png,.doc,.docx"
          onChange={(e) => handleFileChange(fileType, e)}
          className="file-input"
          id={`file-${fileType}`}
        />
        <label htmlFor={`file-${fileType}`} className="file-input-label">
          {f.selectFile}
        </label>
        {file && (
          <div className="file-info">
            <span className="file-name">{file.fileName}</span>
            <span className="file-size">({formatFileSize(file.fileSize)})</span>
            <button
              type="button"
              className="btn-remove-file"
              onClick={() => handleRemoveFile(fileType)}
            >
              {f.removeFile}
            </button>
          </div>
        )}
        {!file && (
          <span className="file-placeholder">{f.noFileSelected}</span>
        )}
      </div>
    </div>
  );

  return (
    <section className="form-section">
      <SectionHeader title={t.sections.documents} />
      <div className="documents-grid">
        <FileUploadField
          label={f.billOfLading}
          fileType="billOfLading"
          file={documents.billOfLading}
          inputRef={billOfLadingRef}
        />
        <FileUploadField
          label={f.commercialInvoice}
          fileType="commercialInvoice"
          file={documents.commercialInvoice}
          inputRef={commercialInvoiceRef}
        />
        <FileUploadField
          label={f.exportInvoice}
          fileType="exportInvoice"
          file={documents.exportInvoice}
          inputRef={exportInvoiceRef}
        />
        <FileUploadField
          label={f.otherDocument}
          fileType="otherDocument"
          file={documents.otherDocument}
          inputRef={otherDocumentRef}
        />
      </div>
    </section>
  );
};

export default DocumentsSection;
