import React from 'react';
import FormField from './FormField';
import SectionHeader from './SectionHeader';
import { VoyageBL } from '../types';
import { Translations } from '../types/translations';

interface Props {
  data: VoyageBL;
  onChange: (data: VoyageBL) => void;
  t: Translations;
}

const VoyageBLSection: React.FC<Props> = ({ data, onChange, t }) => {
  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    onChange({ ...data, [e.target.name]: e.target.value });
  };

  const f = t.fields;

  return (
    <section className="form-section">
      <SectionHeader title={t.sections.voyageBL} />
      <div className="grid grid-2">
        <FormField label={f.voyageNumber} name="voyageNumber" value={data.voyageNumber} onChange={handleChange} />
        <FormField label={f.vesselName} name="vesselName" value={data.vesselName} onChange={handleChange} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.transit} name="transit" value={data.transit} onChange={handleChange} type="select" options={['Y', 'N']} />
        <FormField label={f.consolidation} name="consolidation" value={data.consolidation} onChange={handleChange} type="select" options={['Y', 'N']} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.blNumber} name="blNumber" value={data.blNumber} onChange={handleChange} required />
        <FormField label={f.shippingLine} name="shippingLine" value={data.shippingLine} onChange={handleChange} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.portOfLoading} name="portOfLoading" value={data.portOfLoading} onChange={handleChange} />
        <FormField label={f.portOfDischarge} name="portOfDischarge" value={data.portOfDischarge} onChange={handleChange} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.countryOfDestination} name="countryOfDestination" value={data.countryOfDestination} onChange={handleChange} />
        <FormField label={f.freightForwarder} name="freightForwarder" value={data.freightForwarder} onChange={handleChange} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.shipper} name="shipper" value={data.shipper} onChange={handleChange} />
        <FormField label={f.consignee} name="consignee" value={data.consignee} onChange={handleChange} />
      </div>
      <div className="grid grid-2">
        <FormField label={f.representative} name="representative" value={data.representative} onChange={handleChange} />
        <FormField label={f.representativeEmail} name="representativeEmail" value={data.representativeEmail} onChange={handleChange} type="email" />
      </div>
      <div className="grid grid-3">
        <FormField label={f.grossWeight} name="grossWeight" value={data.grossWeight} onChange={handleChange} type="number" />
        <FormField label={f.volume} name="volume" value={data.volume} onChange={handleChange} type="number" />
        <div />
      </div>
      <div className="grid grid-2">
        <FormField label={f.onBoardDate} name="onBoardDate" value={data.onBoardDate} onChange={handleChange} type="date" />
        <FormField label={f.issueDate} name="issueDate" value={data.issueDate} onChange={handleChange} type="date" />
      </div>
    </section>
  );
};

export default VoyageBLSection;
