import React from 'react';
import FormField from './FormField';
import SectionHeader from './SectionHeader';
import { ContainerCargo, ConventionalCargo, Vehicle, BulkCargo } from '../types';
import { Translations } from '../types/translations';

type CargoType = 'container' | 'conventional' | 'vehicle' | 'bulk';

interface Props {
  cargoType: CargoType;
  containers: ContainerCargo[];
  conventionals: ConventionalCargo[];
  vehicles: Vehicle[];
  bulks: BulkCargo[];
  onCargoTypeChange: (type: CargoType) => void;
  onContainersChange: (data: ContainerCargo[]) => void;
  onConventionalsChange: (data: ConventionalCargo[]) => void;
  onVehiclesChange: (data: Vehicle[]) => void;
  onBulksChange: (data: BulkCargo[]) => void;
  t: Translations;
}

const CONTAINER_SIZES = ["10'", "20'", "40'", "45'"];
const CONTAINER_TYPES = ['High Cube', 'Dry', 'Reefer', 'Citerne', 'Open Side', 'Open Top', 'Flat Rack', 'Autres'];

const emptyContainer = (): ContainerCargo => ({
  containerSize: '', containerType: '', tareWeight: '',
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const emptyConventional = (): ConventionalCargo => ({
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const emptyVehicle = (): Vehicle => ({
  chassisNumber: '', brand: '', countryOfOrigin: '', netWeight: '', description: '',
});

const emptyBulk = (): BulkCargo => ({
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const MAX_CARGO_ITEMS = 5;

const CargoSection: React.FC<Props> = ({
  cargoType, containers, conventionals, vehicles, bulks,
  onCargoTypeChange, onContainersChange, onConventionalsChange, onVehiclesChange, onBulksChange,
  t,
}) => {
  const f = t.fields;
  const s = t.sections;

  // Container handlers
  const handleContainerChange = (index: number, e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const updated = containers.map((item, i) =>
      i === index ? { ...item, [e.target.name]: e.target.value } : item
    );
    onContainersChange(updated);
  };

  const addContainer = () => {
    if (containers.length < MAX_CARGO_ITEMS) {
      onContainersChange([...containers, emptyContainer()]);
    }
  };

  const removeContainer = (index: number) => {
    if (containers.length > 1) {
      onContainersChange(containers.filter((_, i) => i !== index));
    }
  };

  // Conventional handlers
  const handleConventionalChange = (index: number, e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const updated = conventionals.map((item, i) =>
      i === index ? { ...item, [e.target.name]: e.target.value } : item
    );
    onConventionalsChange(updated);
  };

  const addConventional = () => {
    if (conventionals.length < MAX_CARGO_ITEMS) {
      onConventionalsChange([...conventionals, emptyConventional()]);
    }
  };

  const removeConventional = (index: number) => {
    if (conventionals.length > 1) {
      onConventionalsChange(conventionals.filter((_, i) => i !== index));
    }
  };

  // Vehicle handlers
  const handleVehicleChange = (index: number, e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const updated = vehicles.map((item, i) =>
      i === index ? { ...item, [e.target.name]: e.target.value } : item
    );
    onVehiclesChange(updated);
  };

  const addVehicle = () => {
    if (vehicles.length < MAX_CARGO_ITEMS) {
      onVehiclesChange([...vehicles, emptyVehicle()]);
    }
  };

  const removeVehicle = (index: number) => {
    if (vehicles.length > 1) {
      onVehiclesChange(vehicles.filter((_, i) => i !== index));
    }
  };

  // Bulk handlers
  const handleBulkChange = (index: number, e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const updated = bulks.map((item, i) =>
      i === index ? { ...item, [e.target.name]: e.target.value } : item
    );
    onBulksChange(updated);
  };

  const addBulk = () => {
    if (bulks.length < MAX_CARGO_ITEMS) {
      onBulksChange([...bulks, emptyBulk()]);
    }
  };

  const removeBulk = (index: number) => {
    if (bulks.length > 1) {
      onBulksChange(bulks.filter((_, i) => i !== index));
    }
  };

  return (
    <section className="form-section">
      <SectionHeader title={t.sections.cargoDetails} />

      <div className="cargo-tabs">
        {(['container', 'conventional', 'vehicle', 'bulk'] as CargoType[]).map((type) => (
          <button
            key={type}
            type="button"
            className={`cargo-tab${cargoType === type ? ' active' : ''}`}
            onClick={() => onCargoTypeChange(type)}
          >
            {s[type as keyof typeof s]}
          </button>
        ))}
      </div>

      {cargoType === 'container' && (
        <div className="cargo-fields">
          <div className="subsection-header-row">
            <h3 className="subsection-title">{s.container}</h3>
            {containers.length < MAX_CARGO_ITEMS && (
              <button type="button" className="btn-add" onClick={addContainer}>
                + {t.actions.addItem || 'Add Item'}
              </button>
            )}
          </div>
          {containers.map((container, index) => (
            <div key={index} className="declared-item">
              <div className="item-header">
                <span className="item-number">#{index + 1}</span>
                {containers.length > 1 && (
                  <button type="button" className="btn-remove" onClick={() => removeContainer(index)}>
                    {t.actions.removeItem || 'Remove'}
                  </button>
                )}
              </div>
              <div className="grid grid-2">
                <FormField label={f.containerSize} name="containerSize" value={container.containerSize} onChange={(e) => handleContainerChange(index, e)} type="select" options={CONTAINER_SIZES} />
                <FormField label={f.containerType} name="containerType" value={container.containerType} onChange={(e) => handleContainerChange(index, e)} type="select" options={CONTAINER_TYPES} />
              </div>
              <div className="grid grid-3">
                <FormField label={f.tareWeight} name="tareWeight" value={container.tareWeight} onChange={(e) => handleContainerChange(index, e)} type="number" />
                <FormField label={f.countryOfOrigin} name="countryOfOrigin" value={container.countryOfOrigin} onChange={(e) => handleContainerChange(index, e)} />
                <FormField label={f.packagingType} name="packagingType" value={container.packagingType} onChange={(e) => handleContainerChange(index, e)} />
              </div>
              <div className="grid grid-2">
                <FormField label={f.numberOfPackages} name="numberOfPackages" value={container.numberOfPackages} onChange={(e) => handleContainerChange(index, e)} type="number" />
                <FormField label={f.netWeight} name="netWeight" value={container.netWeight} onChange={(e) => handleContainerChange(index, e)} type="number" />
              </div>
              <FormField label={f.description} name="description" value={container.description} onChange={(e) => handleContainerChange(index, e)} type="textarea" />
            </div>
          ))}
        </div>
      )}

      {cargoType === 'conventional' && (
        <div className="cargo-fields">
          <div className="subsection-header-row">
            <h3 className="subsection-title">{s.conventional}</h3>
            {conventionals.length < MAX_CARGO_ITEMS && (
              <button type="button" className="btn-add" onClick={addConventional}>
                + {t.actions.addItem || 'Add Item'}
              </button>
            )}
          </div>
          {conventionals.map((conventional, index) => (
            <div key={index} className="declared-item">
              <div className="item-header">
                <span className="item-number">#{index + 1}</span>
                {conventionals.length > 1 && (
                  <button type="button" className="btn-remove" onClick={() => removeConventional(index)}>
                    {t.actions.removeItem || 'Remove'}
                  </button>
                )}
              </div>
              <div className="grid grid-3">
                <FormField label={f.countryOfOrigin} name="countryOfOrigin" value={conventional.countryOfOrigin} onChange={(e) => handleConventionalChange(index, e)} />
                <FormField label={f.packagingType} name="packagingType" value={conventional.packagingType} onChange={(e) => handleConventionalChange(index, e)} />
                <FormField label={f.numberOfPackages} name="numberOfPackages" value={conventional.numberOfPackages} onChange={(e) => handleConventionalChange(index, e)} type="number" />
              </div>
              <div className="grid grid-2">
                <FormField label={f.netWeight} name="netWeight" value={conventional.netWeight} onChange={(e) => handleConventionalChange(index, e)} type="number" />
                <div />
              </div>
              <FormField label={f.description} name="description" value={conventional.description} onChange={(e) => handleConventionalChange(index, e)} type="textarea" />
            </div>
          ))}
        </div>
      )}

      {cargoType === 'vehicle' && (
        <div className="cargo-fields">
          <div className="subsection-header-row">
            <h3 className="subsection-title">{s.vehicle}</h3>
            {vehicles.length < MAX_CARGO_ITEMS && (
              <button type="button" className="btn-add" onClick={addVehicle}>
                + {t.actions.addItem || 'Add Item'}
              </button>
            )}
          </div>
          {vehicles.map((vehicle, index) => (
            <div key={index} className="declared-item">
              <div className="item-header">
                <span className="item-number">#{index + 1}</span>
                {vehicles.length > 1 && (
                  <button type="button" className="btn-remove" onClick={() => removeVehicle(index)}>
                    {t.actions.removeItem || 'Remove'}
                  </button>
                )}
              </div>
              <div className="grid grid-2">
                <FormField label={f.chassisNumber} name="chassisNumber" value={vehicle.chassisNumber} onChange={(e) => handleVehicleChange(index, e)} />
                <FormField label={f.brand} name="brand" value={vehicle.brand} onChange={(e) => handleVehicleChange(index, e)} />
              </div>
              <div className="grid grid-2">
                <FormField label={f.countryOfOrigin} name="countryOfOrigin" value={vehicle.countryOfOrigin} onChange={(e) => handleVehicleChange(index, e)} />
                <FormField label={f.netWeight} name="netWeight" value={vehicle.netWeight} onChange={(e) => handleVehicleChange(index, e)} type="number" />
              </div>
              <FormField label={f.description} name="description" value={vehicle.description} onChange={(e) => handleVehicleChange(index, e)} type="textarea" />
            </div>
          ))}
        </div>
      )}

      {cargoType === 'bulk' && (
        <div className="cargo-fields">
          <div className="subsection-header-row">
            <h3 className="subsection-title">{s.bulk}</h3>
            {bulks.length < MAX_CARGO_ITEMS && (
              <button type="button" className="btn-add" onClick={addBulk}>
                + {t.actions.addItem || 'Add Item'}
              </button>
            )}
          </div>
          {bulks.map((bulk, index) => (
            <div key={index} className="declared-item">
              <div className="item-header">
                <span className="item-number">#{index + 1}</span>
                {bulks.length > 1 && (
                  <button type="button" className="btn-remove" onClick={() => removeBulk(index)}>
                    {t.actions.removeItem || 'Remove'}
                  </button>
                )}
              </div>
              <div className="grid grid-3">
                <FormField label={f.countryOfOrigin} name="countryOfOrigin" value={bulk.countryOfOrigin} onChange={(e) => handleBulkChange(index, e)} />
                <FormField label={f.packagingType} name="packagingType" value={bulk.packagingType} onChange={(e) => handleBulkChange(index, e)} />
                <FormField label={f.numberOfPackages} name="numberOfPackages" value={bulk.numberOfPackages} onChange={(e) => handleBulkChange(index, e)} type="number" />
              </div>
              <div className="grid grid-2">
                <FormField label={f.netWeight} name="netWeight" value={bulk.netWeight} onChange={(e) => handleBulkChange(index, e)} type="number" />
                <div />
              </div>
              <FormField label={f.description} name="description" value={bulk.description} onChange={(e) => handleBulkChange(index, e)} type="textarea" />
            </div>
          ))}
        </div>
      )}
    </section>
  );
};

export default CargoSection;
