import React, { useState, useRef } from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import './App.css';
import CompanySection from './components/CompanySection';
import VoyageBLSection from './components/VoyageBLSection';
import CargoSection from './components/CargoSection';
import ExportDeclarationSection from './components/ExportDeclarationSection';
import DocumentsSection from './components/DocumentsSection';
import { ECTNFormData, ContainerCargo, ConventionalCargo, Vehicle, BulkCargo } from './types';
import { translations } from './types/translations';

const emptyContainer = (): ContainerCargo => ({
  containerSize: '', containerType: '', tareWeight: '',
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const emptyConventional = (): ConventionalCargo => ({
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const emptyVehicle = (): Vehicle => ({
  chassisNumber: '', brand: '', countryOfOrigin: '', netWeight: '', description: '',
});

const emptyBulk = (): BulkCargo => ({
  countryOfOrigin: '', packagingType: '', numberOfPackages: '', netWeight: '', description: '',
});

const initialData: ECTNFormData = {
  language: 'en',
  companyInfo: {
    companyName: '',
    companyEmail: '',
  },
  voyageBL: {
    voyageNumber: '', vesselName: '', transit: '', consolidation: '',
    blNumber: '', portOfLoading: '', portOfDischarge: '', countryOfDestination: '',
    shipper: '', representative: '', representativeEmail: '', consignee: '',
    shippingLine: '', freightForwarder: '', grossWeight: '', volume: '',
    onBoardDate: '', issueDate: '',
  },
  cargoType: 'container',
  container: [emptyContainer()],
  conventional: [emptyConventional()],
  vehicle: [emptyVehicle()],
  bulk: [emptyBulk()],
  declarationInfo: {
    declarationNumber: '', declarationDate: '', placeOfDeclaration: '', remarks: '',
  },
  declaredItems: [
    { hsCode: '', currency: '', declaredWeight: '', customsRegime: '', description: '' },
  ],
  incotermValues: {
    incoterm: '', currency: '', fobValue: '', seaFreight: '', insurance: '', otherExpenses: '',
  },
  documents: {
    billOfLading: null,
    commercialInvoice: null,
    exportInvoice: null,
    otherDocument: null,
  },
};

function App() {
  const [form, setForm] = useState<ECTNFormData>(initialData);
  const [submitted, setSubmitted] = useState(false);
  const [recaptchaError, setRecaptchaError] = useState<string>('');
  const recaptchaRef = useRef<ReCAPTCHA>(null);

  const t = translations[form.language];

  // reCAPTCHA site key -  remplacer par votre cl publique
  const RECAPTCHA_SITE_KEY = process.env.REACT_APP_RECAPTCHA_SITE_KEY || '6LeIxAcTAAAAAJcZVRqyHh71UMIEGNQ_MXjiZKhI'; // Cl de test par dfaut

  const handleLanguageToggle = () => {
    setForm((prev) => ({ ...prev, language: prev.language === 'en' ? 'fr' : 'en' }));
    // Reset reCAPTCHA when language changes
    if (recaptchaRef.current) {
      recaptchaRef.current.reset();
    }
    setRecaptchaError('');
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setRecaptchaError('');

    // Vrifier reCAPTCHA
    const recaptchaValue = recaptchaRef.current?.getValue();
    if (!recaptchaValue) {
      setRecaptchaError(t.errors?.recaptchaRequired || 'Please complete the reCAPTCHA verification');
      return;
    }

    try {
      // Envoi au backend avec token reCAPTCHA
      const response = await fetch('http://localhost:5000/api/forms', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...form,
          recaptchaToken: recaptchaValue,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        if (errorData.field === 'recaptcha') {
          setRecaptchaError(errorData.error || 'reCAPTCHA verification failed');
        } else {
          console.error('Form submission error:', errorData);
          alert('Form submission failed. Please try again.');
        }
        return;
      }

      const result = await response.json();
      console.log('Form submitted successfully:', result);
      
      setSubmitted(true);
      setTimeout(() => {
        setSubmitted(false);
        if (recaptchaRef.current) {
          recaptchaRef.current.reset();
        }
      }, 4000);
    } catch (error) {
      console.error('Form submission error:', error);
      alert('Network error. Please try again.');
    }
  };

  const handleReset = () => {
    setForm({ ...initialData, language: form.language });
  };

  return (
    <div className="app">
      <header className="app-header">
        <div className="header-inner">
          <div className="header-company-block">
            <img
              src={`${process.env.PUBLIC_URL || ''}/logo.png`}
              alt=""
              className="header-company-logo"
              onError={(e) => {
                const el = e.currentTarget;
                el.onerror = null;
                el.src = `${process.env.PUBLIC_URL || ''}/logo.svg`;
              }}
            />
            <span className="header-company-name">2kmlogistics</span>
          </div>
          <button
            type="button"
            className="lang-toggle"
            onClick={handleLanguageToggle}
          >
            {form.language === 'en' ? 'FR' : 'EN'}
          </button>
        </div>
      </header>

      <div className="page-title-section">
        <div className="page-title-inner">
          <h1 className="page-title">{t.title}</h1>
          <p className="page-subtitle">Electronic Cargo Tracking Note</p>
        </div>
      </div>

      <main className="app-main">
        <form onSubmit={handleSubmit} noValidate>
          <CompanySection
            data={form.companyInfo}
            onChange={(companyInfo) => setForm((p) => ({ ...p, companyInfo }))}
            t={t}
          />
          <VoyageBLSection
            data={form.voyageBL}
            onChange={(voyageBL) => setForm((p) => ({ ...p, voyageBL }))}
            t={t}
          />

          <CargoSection
            cargoType={form.cargoType}
            containers={form.container}
            conventionals={form.conventional}
            vehicles={form.vehicle}
            bulks={form.bulk}
            onCargoTypeChange={(cargoType) => setForm((p) => ({ ...p, cargoType }))}
            onContainersChange={(containers) => setForm((p) => ({ ...p, container: containers }))}
            onConventionalsChange={(conventionals) => setForm((p) => ({ ...p, conventional: conventionals }))}
            onVehiclesChange={(vehicles) => setForm((p) => ({ ...p, vehicle: vehicles }))}
            onBulksChange={(bulks) => setForm((p) => ({ ...p, bulk: bulks }))}
            t={t}
          />

          <ExportDeclarationSection
            declarationInfo={form.declarationInfo}
            declaredItems={form.declaredItems}
            incotermValues={form.incotermValues}
            onDeclarationInfoChange={(declarationInfo) => setForm((p) => ({ ...p, declarationInfo }))}
            onDeclaredItemsChange={(declaredItems) => setForm((p) => ({ ...p, declaredItems }))}
            onIncotermValuesChange={(incotermValues) => setForm((p) => ({ ...p, incotermValues }))}
            t={t}
          />

          <DocumentsSection
            documents={form.documents}
            onChange={(documents) => setForm((p) => ({ ...p, documents }))}
            t={t}
          />

          <div className="recaptcha-section">
            <ReCAPTCHA
              ref={recaptchaRef}
              sitekey={RECAPTCHA_SITE_KEY}
              onChange={() => setRecaptchaError('')}
              onExpired={() => setRecaptchaError(t.errors?.recaptchaExpired || 'reCAPTCHA expired. Please try again.')}
              onError={() => setRecaptchaError(t.errors?.recaptchaError || 'reCAPTCHA error. Please try again.')}
            />
            {recaptchaError && (
              <div className="recaptcha-error">{recaptchaError}</div>
            )}
          </div>

          <div className="form-actions">
            <button type="button" className="btn-secondary" onClick={handleReset}>
              {t.actions.reset}
            </button>
            <button type="submit" className="btn-primary">
              {submitted ? '' : t.actions.submit}
            </button>
          </div>
        </form>
      </main>
    </div>
  );
}

export default App;
