import React from 'react';
import FormField from './FormField';
import SectionHeader from './SectionHeader';
import { DeclarationInfo, DeclaredItem, IncotermValues } from '../types';
import { Translations } from '../types/translations';

interface Props {
  declarationInfo: DeclarationInfo;
  declaredItems: DeclaredItem[];
  incotermValues: IncotermValues;
  onDeclarationInfoChange: (data: DeclarationInfo) => void;
  onDeclaredItemsChange: (items: DeclaredItem[]) => void;
  onIncotermValuesChange: (data: IncotermValues) => void;
  t: Translations;
}

const INCOTERMS = ['EXW', 'FCA', 'CPT', 'CIP', 'DAP', 'DPU', 'DDP', 'FAS', 'FOB', 'CFR', 'CIF'];
const CURRENCIES = ['USD', 'EUR', 'XOF', 'GBP', 'JPY', 'CNY'];
const CUSTOMS_REGIMES = ['IM4', 'EX1', 'IM7', 'IM1', 'EX3'];

const emptyItem = (): DeclaredItem => ({
  hsCode: '', currency: '', declaredWeight: '', customsRegime: '', description: '',
});

const ExportDeclarationSection: React.FC<Props> = ({
  declarationInfo, declaredItems, incotermValues,
  onDeclarationInfoChange, onDeclaredItemsChange, onIncotermValuesChange,
  t,
}) => {
  const f = t.fields;

  const handleDeclaration = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) =>
    onDeclarationInfoChange({ ...declarationInfo, [e.target.name]: e.target.value });

  const handleIncoterm = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) =>
    onIncotermValuesChange({ ...incotermValues, [e.target.name]: e.target.value });

  const handleItemChange = (index: number, e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const updated = declaredItems.map((item, i) =>
      i === index ? { ...item, [e.target.name]: e.target.value } : item
    );
    onDeclaredItemsChange(updated);
  };

  const addItem = () => onDeclaredItemsChange([...declaredItems, emptyItem()]);

  const removeItem = (index: number) =>
    onDeclaredItemsChange(declaredItems.filter((_, i) => i !== index));

  return (
    <section className="form-section">
      <SectionHeader title={t.sections.exportDeclaration} />

      <div className="subsection">
        <h3 className="subsection-title">{t.sections.declarationInfo}</h3>
        <div className="grid grid-2">
          <FormField label={f.declarationNumber} name="declarationNumber" value={declarationInfo.declarationNumber} onChange={handleDeclaration} />
          <FormField label={f.declarationDate} name="declarationDate" value={declarationInfo.declarationDate} onChange={handleDeclaration} type="date" />
        </div>
        <div className="grid grid-2">
          <FormField label={f.placeOfDeclaration} name="placeOfDeclaration" value={declarationInfo.placeOfDeclaration} onChange={handleDeclaration} />
          <FormField label={f.remarks} name="remarks" value={declarationInfo.remarks} onChange={handleDeclaration} />
        </div>
      </div>

      <div className="subsection">
        <div className="subsection-header-row">
          <h3 className="subsection-title">{t.sections.declaredItem}</h3>
          <button type="button" className="btn-add" onClick={addItem}>+ {t.actions.addItem}</button>
        </div>
        {declaredItems.map((item, index) => (
          <div key={index} className="declared-item">
            <div className="item-header">
              <span className="item-number">#{index + 1}</span>
              {declaredItems.length > 1 && (
                <button type="button" className="btn-remove" onClick={() => removeItem(index)}>
                  {t.actions.removeItem}
                </button>
              )}
            </div>
            <div className="grid grid-3">
              <FormField label={f.hsCode} name="hsCode" value={item.hsCode} onChange={(e) => handleItemChange(index, e)} />
              <FormField label={f.currency} name="currency" value={item.currency} onChange={(e) => handleItemChange(index, e)} type="select" options={CURRENCIES} />
              <FormField label={f.declaredWeight} name="declaredWeight" value={item.declaredWeight} onChange={(e) => handleItemChange(index, e)} type="number" />
            </div>
            <div className="grid grid-2">
              <FormField label={f.customsRegime} name="customsRegime" value={item.customsRegime} onChange={(e) => handleItemChange(index, e)} type="select" options={CUSTOMS_REGIMES} />
              <div />
            </div>
            <FormField label={f.description} name="description" value={item.description} onChange={(e) => handleItemChange(index, e)} type="textarea" />
          </div>
        ))}
      </div>

      <div className="subsection">
        <h3 className="subsection-title">{t.sections.incotermValues}</h3>
        <div className="grid grid-3">
          <FormField label={f.incoterm} name="incoterm" value={incotermValues.incoterm} onChange={handleIncoterm} type="select" options={INCOTERMS} />
          <FormField label={f.currency} name="currency" value={incotermValues.currency} onChange={handleIncoterm} type="select" options={CURRENCIES} />
          <FormField label={f.fobValue} name="fobValue" value={incotermValues.fobValue} onChange={handleIncoterm} type="number" />
        </div>
        <div className="grid grid-3">
          <FormField label={f.seaFreight} name="seaFreight" value={incotermValues.seaFreight} onChange={handleIncoterm} type="number" />
          <FormField label={f.insurance} name="insurance" value={incotermValues.insurance} onChange={handleIncoterm} type="number" />
          <FormField label={f.otherExpenses} name="otherExpenses" value={incotermValues.otherExpenses} onChange={handleIncoterm} type="number" />
        </div>
      </div>
    </section>
  );
};

export default ExportDeclarationSection;
